import React, { useState } from "react";
import { Col, Row } from "react-bootstrap";
import ModalImage from "react-modal-image";
import { MdKeyboardArrowLeft, MdKeyboardArrowRight } from "react-icons/md";
import * as certificationImages from "../../Assets/certif"; // Import all certification images

const certifications = [
  { name: "Java 8 Essential Training", file: "java8.pdf", image: certificationImages.java8 },
  // Add more certifications as needed
];

function Certifications() {
  const [isOpen, setIsOpen] = useState(false);
  const [selectedCert, setSelectedCert] = useState(null);

  const openCertModal = (cert) => {
    setSelectedCert(cert);
    setIsOpen(true);
  };

  const closeCertModal = () => {
    setSelectedCert(null);
    setIsOpen(false);
  };

  const handlePrevCert = () => {
    const currentIndex = certifications.findIndex(
      (cert) => cert.image === selectedCert.image
    );
    const prevCert = certifications[currentIndex - 1];
    if (prevCert) setSelectedCert(prevCert);
  };

  const handleNextCert = () => {
    const currentIndex = certifications.findIndex(
      (cert) => cert.image === selectedCert.image
    );
    const nextCert = certifications[currentIndex + 1];
    if (nextCert) setSelectedCert(nextCert);
  };

  return (
    <>
      <h1 className="project-heading">
        <strong className="purple">Certifications</strong>
      </h1>
      <Row style={{ justifyContent: "center", paddingBottom: "50px" }}>
        {certifications.map((cert, index) => (
          <Col key={index} xs={4} md={2} className="tech-icons">
            <div onClick={() => openCertModal(cert)} style={{ width: "100px", height: "100px", cursor: "pointer" }}>
                <img src={`../../Assets/certif/${cert.image}`} alt={cert.name} className="img-fluid" style={{ width: "50%", height: "50%", transform: "scale(0.7)" }} />
                <span>{cert.name}</span>
            </div>
          </Col>
        ))}
      </Row>

      <ModalImage
        small={`../../Assets/certif/${selectedCert?.image}`}
        large={`../../Assets/certif/${selectedCert?.image}`}
        alt={selectedCert?.name}
        isOpen={isOpen}
        onRequestClose={closeCertModal}
        hideDownload={true}
        hideZoom={true}
        hideRotate={true}
        modalStyles={{
          overlay: {
            backgroundColor: "rgba(0, 0, 0, 0.75)",
          },
          content: {
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            border: "none",
            borderRadius: "8px",
            outline: "none",
            padding: "20px",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            maxWidth: "90%",
            maxHeight: "90%",
          },
        }}
        imageStyles={{ cursor: "auto" }}
      >
        <div style={{ display: "flex", alignItems: "center" }}>
          <button className="close-btn" onClick={closeCertModal}>
            Close
          </button>
          <div style={{ flex: 1 }}></div>
          <MdKeyboardArrowLeft
            size={32}
            style={{ cursor: "pointer" }}
            onClick={handlePrevCert}
          />
          <MdKeyboardArrowRight
            size={32}
            style={{ cursor: "pointer" }}
            onClick={handleNextCert}
          />
        </div>
      </ModalImage>
    </>
  );
}

export default Certifications;
